<template>
  <div class="Admin">
    <div class="header sticky">
      <nav>
        <router-link to="/">Home | </router-link>
        <router-link to="/login">Login</router-link>
        <button v-if="!isNotVerified" class="logout-btn" @click="logout()">Logout</button>
      </nav>
    </div>
  </div>
  <div class="content">
    <div class="login-page" v-if="isVerified()">
      <form @click.prevent="onSubmit">
        <div class="imgcontainer">
          <img src="../assets/img_avatar2.png" alt="Avatar" class="avatar" />
        </div>

        <div class="container">
          <label for="uname"><b>Username</b></label>
          <input
            v-model="username"
            type="text"
            placeholder="Enter Username"
            name="uname"
            required
          />

          <label for="psw"><b>Password</b></label>
          <input
            v-model="password"
            type="password"
            placeholder="Enter Password"
            name="psw"
            required
          />
          <div style="display:block;">
            <button class="login-btn" type="submit" @click="attemptLogin()">Login <div class="small-loader" style="float: right" v-if="attemptLoginloading"></div></button>
          </div>
          
          <label>
            <input type="checkbox" checked="checked" name="remember" /> Remember
            me
          </label>
        </div>

        <div class="container" style="background-color: #f1f1f1">
          <router-link to="/"
            ><button class="cancelbtn">Cancel</button></router-link
          >
          <span class="psw">Forgot <a href="#">password?</a></span>
        </div>
      </form>
    </div>

    <div v-else>
      <div class="tab">
        <button
          class="tablinks"
          :class="{ active: tabcontent('userDetails') }"
          @click="select('userDetails')"
        >
          <p class="tab-p">User details</p>
        </button>
        <button
          class="tablinks"
          :class="{ active: tabcontent('searchDetails') }"
          @click="select('searchDetails')"
        >
          <p class="tab-p">Search details</p>
        </button>
      </div>

      <div v-if="isLoading()" class="loader"></div>
      <div v-else>
        <div v-if="tabcontent('userDetails')" class="tabcontent">
          <p>User details</p>
          <table id="customers">
            <tr>
              <th>IP</th>
              <th>Access time</th>
              <th>Location</th>
              <th>Network</th>
              <th>City</th>
            </tr>
            <tr v-for="entry in userData" :key="entry._id">
              <td>{{ entry.ip }}</td>
              <td>{{ entry.time }}</td>
              <td>{{ entry.loc }}</td>
              <td>{{ entry.org }}</td>
              <td>{{ entry.city }}</td>
            </tr>
          </table>
        </div>

        <div v-if="tabcontent('searchDetails')" class="tabcontent">
          <p>Search details</p>
          <table id="customers">
            <tr>
              <th>IP</th>
              <th>Query</th>
            </tr>
            <tr v-for="entry in searchData" :key="entry._id">
              <td>{{ entry.ip }}</td>
              <td>{{ entry.searchText }}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginView",
  data() {
    return {
      serverURL: "https://torrent4you.onrender.com/",
      // serverURL: "http://localhost:1234/",
      userData: [],
      searchData: [],
      tabName: null,
      loading: false,
      username: null,
      password: null,
      isNotVerified: false,
      attemptLoginloading: false,
    };
  },
  async created() {
    const token = window.localStorage.getItem("devil-session-token");
    if (token) {
      await this.login();
    } else {
      this.isNotVerified = true;
    }
  },
  methods: {
    logout() {
      window.localStorage.removeItem("devil-session-token");
      this.isNotVerified = true;

    },
    isVerified() {
      return this.isNotVerified;
    },

    isLoading() {
      return this.loading;
    },

    async attemptLogin() {
      this.attemptLoginloading = true;
      await axios
        .post(
          this.serverURL+"login",
          { username: this.username, password: this.password },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then(async (res) => {
          const data = res.data;
          const token = data[0];
          window.localStorage.setItem("devil-session-token", token.token);
          await this.login();
          this.attemptLoginloading = false;
        })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response.headers);
          } else if (error.request) {
            console.log(error.request);
          } else {
            console.log(error.message);
          }
          this.attemptLoginloading = false;
        });
    },

    async login() {
      this.loading = true;
      const token = window.localStorage.getItem("devil-session-token");

      if (token) {
        await axios
          .post(
            this.serverURL+"check-token",
            { token: token },
            {
              headers: {
                "Content-Type": "application/json",
              },
            }
          )
          .then(async () => {
            this.isNotVerified = false;
            this.tabName = "userdetails";
            await this.getItems();
            this.loading = false;
          })
          .catch(() => {
            this.isNotVerified = true;
          });
      } else {
        await this.attemptLogin();
      }

      this.loading = false;
    },

    async getItems() {
      await this.getSearchData();
      await this.getUserData();
      this.tabName = "userDetails";
    },

    async getUserData() {
      const token = window.localStorage.getItem("devil-session-token");
      await axios
        .post(
          this.serverURL + "get-user-info",
          { token: token },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then((res) => {
          const userData = res.data;

          userData.forEach((data) => {
            this.userData.push(data);
          });
        });
    },

    async getSearchData() {
      const token = window.localStorage.getItem("devil-session-token");
      await axios
        .post(
          this.serverURL + "get-search-info",
          { token: token },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then((res) => {
          const searchData = res.data;

          searchData.forEach((data) => {
            this.searchData.push(data);
          });
        });
    },

    select(tabName) {
      this.tabName = tabName;
    },

    tabcontent(tabName) {
      return this.tabName === tabName;
    },
  },
};
</script>

<style scoped>
.content {
  padding-top: 70px;
}

#customers {
  font-size: 9px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#customers td,
#customers th {
  border: 1px solid #ddd;
  padding: 0px;
}

#customers tr:nth-child(even) {
  background-color: #e4e4e4;
}

#customers tr:hover {
  background-color: #ddd;
}

#customers th {
  padding-top: 8px;
  padding-bottom: 8px;
  text-align: center;
  background-color: #04aa6d;
  color: white;
  width: auto;
  height: auto;
}

.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

/* Style the buttons inside the tab */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}

.loader {
  margin-left: 40px;
  margin-top: 20px;
  border: 4px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid #000000;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 1s ease infinite; /* Safari */
  animation: spin 1s ease infinite;
}

.small-loader {
  border: 4px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid #000000;
  width: 20px;
  height: 20px;
  -webkit-animation: spin 1s ease infinite; /* Safari */
  animation: spin 1s ease infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

form {
  border: 3px solid #f1f1f1;
}

/* Full-width inputs */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.login-btn {
  background-color: #04aa6d;
  color: white;
  padding: 5px 5px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%; 
}

/* Add a hover effect for buttons */
button:hover {
  opacity: 0.8;
}

/* Extra style for the cancel button (red) */
.cancelbtn {
  width: 90px;
  background-color: #f44336;
  align-items: center;
  float: left;
}

/* Center the avatar image inside this container */
.imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
}

/* Avatar image */
img.avatar {
  width: 10%;
  border-radius: 50%;
}

/* Add padding to containers */
.container {
  padding: 16px;
}

/* The "Forgot password" text */
span.psw {
  float: right;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
  span.psw {
    display: block;
    float: none;
  }
  .cancelbtn {
    width: 100%;
  }
}

.tab-p {
  color: black;
}

.logout-btn {
  float: right;
  background-color: rgb(64, 59, 59);
  color: #a8b8ec;
  border: none;
}
</style>

